/* Import the main App styles */
@import url('./App.css');

/* Additional styles for mobile menu and contact form */
.mobile-menu-container {
  display: none;
  align-items: center;
  gap: 1rem;
}

.mobile-menu {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background: var(--bg-primary);
  border-top: 1px solid var(--border-color);
  padding: 1rem 0;
  box-shadow: 0 10px 25px var(--shadow-light);
}

.mobile-nav-link {
  display: block;
  padding: 1rem 2rem;
  color: var(--text-primary);
  text-decoration: none;
  font-weight: 500;
  transition: all 0.3s ease;
  border-bottom: 1px solid var(--border-color);
}

.mobile-nav-link:hover {
  background: var(--bg-secondary);
  color: var(--accent-color);
}

.mobile-nav-link:last-child {
  border-bottom: none;
}

/* Scroll indicator */
.scroll-indicator {
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
  cursor: pointer;
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateX(-50%) translateY(0);
  }
  40% {
    transform: translateX(-50%) translateY(-10px);
  }
  60% {
    transform: translateX(-50%) translateY(-5px);
  }
}

/* Project card enhancements */
.project-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1rem;
}

.project-links {
  display: flex;
  gap: 0.5rem;
}

.project-link {
  background: none;
  border: 2px solid var(--border-color);
  border-radius: 8px;
  padding: 0.5rem;
  cursor: pointer;
  color: var(--text-secondary);
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.project-link:hover {
  border-color: var(--accent-color);
  color: var(--accent-color);
  background: rgba(59, 130, 246, 0.1);
}

.project-features {
  margin-bottom: 1.5rem;
}

.project-features h4 {
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
  color: var(--text-accent);
}

.project-features ul {
  list-style: none;
  padding: 0;
}

.project-features li {
  padding: 0.25rem 0;
  color: var(--text-secondary);
  position: relative;
  padding-left: 1rem;
}

.project-features li::before {
  content: '•';
  color: var(--accent-color);
  position: absolute;
  left: 0;
  font-weight: bold;
}

/* Contact form enhancements */
.contact-intro {
  text-align: center;
  font-size: 1.125rem;
  color: var(--text-secondary);
  margin-bottom: 3rem;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

.form-label {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-bottom: 0.75rem;
  font-weight: 600;
  color: var(--text-accent);
  font-size: 1rem;
}

.submit-button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.contact-success {
  text-align: center;
  padding: 4rem 2rem;
}

.contact-success h2 {
  font-size: 2rem;
  margin: 1rem 0;
  color: var(--text-accent);
}

.contact-success p {
  font-size: 1.125rem;
  color: var(--text-secondary);
  margin-bottom: 2rem;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .mobile-menu-container {
    display: flex;
  }
  
  .nav-menu {
    display: none;
  }
  
  .form-row {
    grid-template-columns: 1fr;
  }
  
  .hero-title {
    font-size: 2.5rem;
  }
  
  .hero-subtitle {
    font-size: 1.25rem;
  }
  
  .hero-description {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .hero-title {
    font-size: 2rem;
  }
  
  .hero-subtitle {
    font-size: 1rem;
  }
  
  .project-header {
    flex-direction: column;
    gap: 1rem;
  }
  
  .project-links {
    align-self: flex-end;
  }
}
