/* CSS Reset and Base Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.6;
  color: var(--text-primary);
  background-color: var(--bg-primary);
  transition: background-color 0.3s ease, color 0.3s ease;
}

/* CSS Variables for Light Mode */
:root {
  --bg-primary: #ffffff;
  --bg-secondary: #f8fafc;
  --bg-accent: #f1f5f9;
  --text-primary: #1e293b;
  --text-secondary: #64748b;
  --text-accent: #0f172a;
  --accent-color: #3b82f6;
  --accent-hover: #2563eb;
  --border-color: #e2e8f0;
  --shadow-light: rgba(0, 0, 0, 0.1);
  --shadow-medium: rgba(0, 0, 0, 0.15);
  --shadow-heavy: rgba(0, 0, 0, 0.25);
  --gradient-primary: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  --gradient-accent: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
}

/* CSS Variables for Dark Mode */
body.dark-mode {
  --bg-primary: #0f172a;
  --bg-secondary: #1e293b;
  --bg-accent: #334155;
  --text-primary: #f8fafc;
  --text-secondary: #cbd5e1;
  --text-accent: #ffffff;
  --accent-color: #60a5fa;
  --accent-hover: #3b82f6;
  --border-color: #475569;
  --shadow-light: rgba(0, 0, 0, 0.3);
  --shadow-medium: rgba(0, 0, 0, 0.4);
  --shadow-heavy: rgba(0, 0, 0, 0.6);
  --gradient-primary: linear-gradient(135deg, #4f46e5 0%, #7c3aed 100%);
  --gradient-accent: linear-gradient(135deg, #ec4899 0%, #ef4444 100%);
}

.App {
  min-height: 100vh;
}

/* Container */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

/* Section Base */
.section {
  padding: 5rem 0;
}

.section-title {
  font-size: 2.5rem;
  font-weight: 700;
  text-align: center;
  margin-bottom: 3rem;
  color: var(--text-accent);
}

/* Navigation */
.navigation {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-bottom: 1px solid var(--border-color);
  z-index: 1000;
  transition: all 0.3s ease;
}

.dark-mode .navigation {
  background: rgba(15, 23, 42, 0.9);
}

.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0;
}

.nav-logo {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--accent-color);
  text-decoration: none;
}

.nav-menu {
  display: flex;
  list-style: none;
  gap: 2rem;
  align-items: center;
}

.nav-link {
  color: var(--text-primary);
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s ease;
  position: relative;
}

.nav-link:hover {
  color: var(--accent-color);
}

.nav-link::after {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  bottom: -5px;
  left: 0;
  background-color: var(--accent-color);
  transition: width 0.3s ease;
}

.nav-link:hover::after {
  width: 100%;
}

.theme-toggle {
  background: none;
  border: 2px solid var(--border-color);
  border-radius: 50px;
  padding: 0.5rem;
  cursor: pointer;
  color: var(--text-primary);
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.theme-toggle:hover {
  border-color: var(--accent-color);
  color: var(--accent-color);
}

.mobile-menu-toggle {
  display: none;
  background: none;
  border: none;
  cursor: pointer;
  color: var(--text-primary);
  font-size: 1.5rem;
}

/* Hero Section */
.hero {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
  background: var(--gradient-primary);
  perspective: 1000px;
  transform-style: preserve-3d;
}

.hero-background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, #667eea 0%, #764ba2 100%);
  background-size: 400% 400%;
  animation: gradientShift 15s ease infinite;
  transform-style: preserve-3d;
  overflow: hidden;
}

.hero-background::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 20%, rgba(255, 255, 255, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(255, 255, 255, 0.08) 0%, transparent 50%),
    radial-gradient(circle at 40% 60%, rgba(255, 255, 255, 0.06) 0%, transparent 50%);
  animation: backgroundOrbs 20s ease-in-out infinite;
}

.hero-background::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    linear-gradient(135deg, transparent 0%, rgba(255, 255, 255, 0.05) 25%, transparent 50%, rgba(255, 255, 255, 0.03) 75%, transparent 100%);
  animation: backgroundWave 25s linear infinite;
  transform-origin: center;
}

@keyframes backgroundOrbs {
  0%, 100% { 
    transform: scale(1) rotate(0deg);
    opacity: 0.7;
  }
  33% { 
    transform: scale(1.2) rotate(120deg);
    opacity: 0.9;
  }
  66% { 
    transform: scale(0.8) rotate(240deg);
    opacity: 0.6;
  }
}

@keyframes backgroundWave {
  0% { transform: translateX(-100%) skewX(0deg); }
  25% { transform: translateX(-50%) skewX(5deg); }
  50% { transform: translateX(0%) skewX(0deg); }
  75% { transform: translateX(50%) skewX(-5deg); }
  100% { transform: translateX(100%) skewX(0deg); }
}

.hero-particles {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: 
    radial-gradient(2px 2px at 20px 30px, rgba(255,255,255,0.4), transparent),
    radial-gradient(2px 2px at 40px 70px, rgba(255,255,255,0.3), transparent),
    radial-gradient(1px 1px at 90px 40px, rgba(255,255,255,0.5), transparent),
    radial-gradient(1px 1px at 130px 80px, rgba(255,255,255,0.4), transparent),
    radial-gradient(2px 2px at 160px 30px, rgba(255,255,255,0.3), transparent),
    radial-gradient(3px 3px at 200px 100px, rgba(255,255,255,0.2), transparent),
    radial-gradient(1px 1px at 250px 50px, rgba(255,255,255,0.4), transparent),
    radial-gradient(2px 2px at 300px 120px, rgba(255,255,255,0.3), transparent);
  background-repeat: repeat;
  background-size: 350px 150px;
  animation: particleFloat 25s linear infinite;
  pointer-events: none;
  z-index: 1;
}

.hero-particles::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 10% 10%, rgba(196, 181, 253, 0.1) 0%, transparent 30%),
    radial-gradient(circle at 90% 20%, rgba(139, 92, 246, 0.08) 0%, transparent 40%),
    radial-gradient(circle at 30% 90%, rgba(167, 139, 250, 0.12) 0%, transparent 35%),
    radial-gradient(circle at 70% 70%, rgba(196, 181, 253, 0.06) 0%, transparent 45%);
  animation: colorOrbs 30s ease-in-out infinite;
}

.hero-particles::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%23ffffff' fill-opacity='0.03'%3E%3Cpath d='m36 34v-4h-2v4h-4v2h4v4h2v-4h4v-2h-4zm0-30V0h-2v4h-4v2h4v4h2V6h4V4h-4zM6 34v-4H4v4H0v2h4v4h2v-4h4v-2H6zM6 4V0H4v4H0v2h4v4h2V6h4V4H6z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
  animation: patternMove 40s linear infinite;
  opacity: 0.4;
}

@keyframes colorOrbs {
  0%, 100% { 
    transform: translateY(0px) scale(1) rotate(0deg);
    opacity: 0.6;
  }
  25% { 
    transform: translateY(-30px) scale(1.1) rotate(90deg);
    opacity: 0.8;
  }
  50% { 
    transform: translateY(-10px) scale(0.9) rotate(180deg);
    opacity: 0.7;
  }
  75% { 
    transform: translateY(-20px) scale(1.05) rotate(270deg);
    opacity: 0.9;
  }
}

@keyframes patternMove {
  0% { transform: translateX(0px) translateY(0px); }
  25% { transform: translateX(-20px) translateY(-10px); }
  50% { transform: translateX(-40px) translateY(-20px); }
  75% { transform: translateX(-60px) translateY(-30px); }
  100% { transform: translateX(-80px) translateY(-40px); }
}

@keyframes particleFloat {
  0% { transform: translateY(0px) translateX(0px) rotate(0deg); }
  25% { transform: translateY(-25px) translateX(15px) rotate(90deg); }
  50% { transform: translateY(-15px) translateX(-10px) rotate(180deg); }
  75% { transform: translateY(-30px) translateX(20px) rotate(270deg); }
  100% { transform: translateY(0px) translateX(0px) rotate(360deg); }
}

.hero-content {
  position: relative;
  z-index: 2;
  text-align: center;
  transform-style: preserve-3d;
}

.dark-mode .hero-background {
  background: linear-gradient(45deg, #4f46e5 0%, #7c3aed 100%);
  background-size: 400% 400%;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-6px);
  }
}

@keyframes floatReverse {
  0%, 100% {
    transform: translateY(-3px);
  }
  50% {
    transform: translateY(3px);
  }
}

@keyframes pulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}

@keyframes gradientShift {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

.hero-content {
  text-align: center;
  color: white;
  z-index: 2;
  position: relative;
}

.hero-title {
  font-size: 4rem;
  font-weight: 700;
  margin-bottom: 1rem;
  opacity: 0;
  transform: translateY(50px);
  animation: fadeInUp 1s ease forwards;
  text-shadow: 
    0 1px 0 rgba(0,0,0,0.3),
    0 2px 10px rgba(0,0,0,0.3),
    0 0 50px rgba(255,255,255,0.1);
  transform-style: preserve-3d;
  transition: all 0.3s ease;
}

.hero-title:hover {
  text-shadow: 
    0 1px 0 rgba(0,0,0,0.3),
    0 2px 10px rgba(0,0,0,0.3),
    0 0 50px rgba(255,255,255,0.3),
    0 5px 20px rgba(255,255,255,0.2);
}

.hero-subtitle {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
  opacity: 0.9;
  font-weight: 400;
  text-shadow: 
    0 1px 3px rgba(0,0,0,0.3),
    0 0 30px rgba(255,255,255,0.1);
  transform-style: preserve-3d;
  transition: all 0.3s ease;
}

.hero-subtitle:hover {
  opacity: 1;
  text-shadow: 
    0 1px 3px rgba(0,0,0,0.3),
    0 0 30px rgba(255,255,255,0.2);
}

.hero-description {
  font-size: 1.125rem;
  margin-bottom: 2.5rem;
  opacity: 0.8;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.6;
  text-shadow: 
    0 1px 2px rgba(0,0,0,0.2),
    0 0 20px rgba(255,255,255,0.05);
  transform-style: preserve-3d;
  transition: all 0.3s ease;
}

.hero-description:hover {
  opacity: 0.95;
  text-shadow: 
    0 1px 2px rgba(0,0,0,0.2),
    0 0 20px rgba(255,255,255,0.1);
}

.hero-cta {
  opacity: 0;
  transform: translateY(50px);
  animation: fadeInUp 1s ease 0.9s forwards;
  display: flex;
  gap: 1rem;
  justify-content: center;
  flex-wrap: wrap;
}

@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.cta-button {
  display: inline-block;
  padding: 1rem 2rem;
  color: white;
  text-decoration: none;
  border-radius: 50px;
  font-weight: 600;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  backdrop-filter: blur(10px);
  border: 2px solid rgba(255, 255, 255, 0.3);
  background: none;
  cursor: pointer;
  font-size: 1rem;
  position: relative;
  overflow: hidden;
  transform-style: preserve-3d;
  box-shadow: 
    0 8px 25px rgba(0, 0, 0, 0.15),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.cta-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.6s ease;
  z-index: 1;
}

.cta-button:hover::before {
  left: 100%;
}

.cta-button::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at center, rgba(255, 255, 255, 0.1) 0%, transparent 70%);
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
  border-radius: 50px;
}

.cta-button:hover::after {
  opacity: 1;
}

.cta-button.primary {
  background: rgba(255, 255, 255, 0.2);
}

.cta-button.secondary {
  background: transparent;
}

.cta-button:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: translateY(-8px) scale(1.05);
  box-shadow: 
    0 20px 40px rgba(0, 0, 0, 0.3),
    0 0 0 1px rgba(255, 255, 255, 0.4),
    inset 0 1px 0 rgba(255, 255, 255, 0.3);
  border-color: rgba(255, 255, 255, 0.6);
}

.cta-button:active {
  transform: translateY(-4px) scale(1.02);
}

.scroll-indicator {
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
  cursor: pointer;
  padding: 1rem;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transform-style: preserve-3d;
  box-shadow: 
    0 8px 25px rgba(0, 0, 0, 0.15),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.scroll-indicator::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at center, rgba(255, 255, 255, 0.1) 0%, transparent 70%);
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
  border-radius: 50%;
}

.scroll-indicator:hover::before {
  opacity: 1;
}

.scroll-indicator:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateX(-50%) translateY(-10px) scale(1.2);
  box-shadow: 
    0 20px 40px rgba(0, 0, 0, 0.3),
    0 0 0 1px rgba(255, 255, 255, 0.4),
    inset 0 1px 0 rgba(255, 255, 255, 0.3);
  border-color: rgba(255, 255, 255, 0.6);
}

/* About Section */
.about {
  background: var(--bg-secondary);
  position: relative;
  overflow: hidden;
}

.about-background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
}

.about-particles {
  position: absolute;
  width: 100%;
  height: 100%;
  background: 
    radial-gradient(circle at 20% 30%, rgba(59, 130, 246, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 80% 70%, rgba(139, 92, 246, 0.1) 0%, transparent 50%);
  animation: aboutParticles 15s ease-in-out infinite;
}

@keyframes aboutParticles {
  0%, 100% { 
    transform: translateY(0px) scale(1);
    opacity: 0.5;
  }
  50% { 
    transform: translateY(-20px) scale(1.1);
    opacity: 0.8;
  }
}

.about-content {
  display: grid;
  grid-template-columns: 1.2fr 0.8fr;
  gap: 5rem;
  align-items: start;
  position: relative;
  z-index: 2;
}

.about-text {
  transform-style: preserve-3d;
}

.about-intro {
  margin-bottom: 2.5rem;
  padding: 2rem;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 20px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  transform-style: preserve-3d;
  transition: all 0.3s ease;
}

.about-intro:hover {
  background: rgba(196, 181, 253, 0.15);
  border-color: rgba(196, 181, 253, 0.5);
  box-shadow: 0 20px 40px rgba(196, 181, 253, 0.3);
}

.about-intro h3 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 1rem;
  background: linear-gradient(135deg, var(--text-accent), var(--accent-color));
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.intro-highlight {
  font-size: 1.25rem;
  color: var(--accent-color);
  font-weight: 600;
  margin: 0;
}

.about-description {
  margin-bottom: 3rem;
}

.about-description p {
  font-size: 1.125rem;
  color: var(--text-secondary);
  margin-bottom: 1.5rem;
  line-height: 1.8;
}

.about-description strong {
  color: var(--accent-color);
  font-weight: 600;
}

.achievements-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5rem;
}

.achievement-card {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  padding: 1.5rem;
  display: flex;
  align-items: center;
  gap: 1rem;
  transition: all 0.3s ease;
  transform-style: preserve-3d;
  backdrop-filter: blur(10px);
}

.achievement-card:hover {
  background: rgba(196, 181, 253, 0.18);
  border-color: rgba(196, 181, 253, 0.5);
  box-shadow: 0 15px 30px rgba(196, 181, 253, 0.35);
}

.achievement-icon {
  width: 50px;
  height: 50px;
  background: linear-gradient(135deg, #c4b5fd, #ddd6fe);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #6b21a8;
  flex-shrink: 0;
  transition: all 0.3s ease;
}

.achievement-card:hover .achievement-icon {
  background: linear-gradient(135deg, #a78bfa, #c4b5fd);
  transform: scale(1.1) rotate(10deg);
  color: white;
}

.achievement-content h4 {
  font-size: 0.95rem;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0 0 0.25rem 0;
}

.achievement-content p {
  font-size: 0.875rem;
  color: var(--accent-color);
  margin: 0;
  font-weight: 500;
}

.about-image {
  text-align: center;
  transform-style: preserve-3d;
}

.profile-card {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 25px;
  padding: 2.5rem;
  box-shadow: 
    0 20px 50px rgba(0, 0, 0, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
  transform: perspective(1000px) rotateY(-5deg) rotateX(2deg);
  transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  border: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  position: relative;
  overflow: hidden;
  transform-style: preserve-3d;
}

.profile-glow {
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(196, 181, 253, 0.2) 0%, transparent 70%);
  animation: profileGlow 4s ease-in-out infinite;
}

@keyframes profileGlow {
  0%, 100% { transform: rotate(0deg) scale(1); opacity: 0.3; }
  50% { transform: rotate(180deg) scale(1.1); opacity: 0.6; }
}

.profile-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(196, 181, 253, 0.12) 0%, rgba(221, 214, 254, 0.08) 100%);
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
  border-radius: 25px;
}

.profile-card:hover::before {
  opacity: 1;
}

.profile-card:hover {
  transform: perspective(1000px) rotateY(0deg) rotateX(0deg) translateY(-15px) scale(1.05);
  box-shadow: 
    0 40px 80px rgba(196, 181, 253, 0.4),
    0 0 0 1px rgba(196, 181, 253, 0.5),
    inset 0 1px 0 rgba(255, 255, 255, 0.3);
  border-color: #c4b5fd;
}

.profile-avatar {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background: linear-gradient(135deg, var(--accent-color), #8b5cf6);
  margin: 0 auto 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  transform-style: preserve-3d;
}

.avatar-text {
  font-size: 2.5rem;
  color: white;
  font-weight: 700;
  z-index: 2;
  position: relative;
}

.avatar-ring {
  position: absolute;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
  border: 2px solid var(--accent-color);
  border-radius: 50%;
  opacity: 0.5;
  animation: avatarRing 3s linear infinite;
}

@keyframes avatarRing {
  0% { transform: rotate(0deg) scale(1); opacity: 0.5; }
  50% { transform: rotate(180deg) scale(1.05); opacity: 0.8; }
  100% { transform: rotate(360deg) scale(1); opacity: 0.5; }
}

.profile-info {
  margin-bottom: 2rem;
  position: relative;
  z-index: 2;
}

.profile-name {
  font-size: 1.75rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  background: linear-gradient(135deg, var(--text-accent), var(--accent-color));
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.profile-role {
  color: var(--accent-color);
  font-weight: 600;
  font-size: 1.125rem;
  margin-bottom: 1rem;
}

.profile-tech {
  display: flex;
  gap: 0.5rem;
  justify-content: center;
  flex-wrap: wrap;
}

.tech-badge {
  background: rgba(59, 130, 246, 0.2);
  color: var(--accent-color);
  padding: 0.25rem 0.75rem;
  border-radius: 20px;
  font-size: 0.875rem;
  font-weight: 500;
  border: 1px solid rgba(59, 130, 246, 0.3);
}

.profile-stats {
  display: flex;
  justify-content: space-around;
  position: relative;
  z-index: 2;
}

.stat {
  text-align: center;
}

.stat-number {
  display: block;
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--accent-color);
  margin-bottom: 0.25rem;
}

.stat-label {
  font-size: 0.875rem;
  color: var(--text-secondary);
  font-weight: 500;
}

/* Projects Section */
.projects {
  background: var(--bg-primary);
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  perspective: 1000px;
}

.project-card {
  background: var(--bg-secondary);
  border-radius: 20px;
  border: 1px solid var(--border-color);
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transform-style: preserve-3d;
  backdrop-filter: blur(10px);
  min-height: 480px;
  display: flex;
  flex-direction: column;
  transform: perspective(1000px) rotateX(0deg) rotateY(0deg);
}

.project-card::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at var(--mouse-x, 50%) var(--mouse-y, 50%), rgba(255, 255, 255, 0.1) 0%, transparent 50%);
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
  border-radius: 20px;
}

.project-card:hover::after {
  opacity: 1;
}

.project-gradient-bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 6px;
  border-radius: 20px 20px 0 0;
  opacity: 0.8;
  transition: all 0.4s ease;
  z-index: 3;
}

.project-card:hover .project-gradient-bg {
  opacity: 1;
  height: 12px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
}

.project-content {
  padding: 2rem;
  position: relative;
  z-index: 2;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.project-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
  transition: left 0.6s ease;
  z-index: 1;
}

.project-card:hover::before {
  left: 100%;
}

.project-card:hover {
  border-color: var(--accent-color);
  transform: perspective(1000px) rotateX(5deg) rotateY(5deg) translateY(-15px) scale(1.02);
  box-shadow: 
    0 35px 60px rgba(0, 0, 0, 0.2),
    0 0 0 1px rgba(255, 255, 255, 0.1),
    inset 0 1px 0 rgba(255, 255, 255, 0.2),
    0 0 40px rgba(var(--accent-rgb), 0.3);
  animation: pulse-glow 2s ease-in-out infinite alternate;
}

@keyframes pulse-glow {
  0% {
    box-shadow: 
      0 35px 60px rgba(0, 0, 0, 0.2),
      0 0 0 1px rgba(255, 255, 255, 0.1),
      inset 0 1px 0 rgba(255, 255, 255, 0.2),
      0 0 40px rgba(var(--accent-rgb), 0.3);
  }
  100% {
    box-shadow: 
      0 40px 70px rgba(0, 0, 0, 0.25),
      0 0 0 1px rgba(255, 255, 255, 0.15),
      inset 0 1px 0 rgba(255, 255, 255, 0.3),
      0 0 60px rgba(var(--accent-rgb), 0.5);
  }
}

.dark-mode .project-card:hover {
  box-shadow: 
    0 35px 60px rgba(0, 0, 0, 0.5),
    0 0 0 1px rgba(255, 255, 255, 0.2),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

.project-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1.5rem;
}

.project-meta {
  flex: 1;
}

.project-category {
  display: inline-block;
  background: var(--accent-color);
  color: white;
  padding: 0.25rem 0.75rem;
  border-radius: 12px;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-bottom: 0.75rem;
}

.project-title {
  font-size: 1.5rem;
  font-weight: 700;
  margin: 0;
  color: var(--text-accent);
  line-height: 1.3;
}

.project-links {
  display: flex;
  gap: 0.75rem;
  margin-left: 1rem;
}

.project-link {
  background: var(--bg-accent);
  border: 2px solid var(--border-color);
  border-radius: 12px;
  padding: 0.75rem;
  cursor: pointer;
  color: var(--text-secondary);
  transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  display: flex;
  align-items: center;
  justify-content: center;
}

.project-link:hover {
  border-color: var(--accent-color);
  color: var(--accent-color);
  background: rgba(59, 130, 246, 0.1);
  transform: translateY(-2px);
}

.project-description {
  color: var(--text-secondary);
  margin-bottom: 1.5rem;
  line-height: 1.7;
  font-size: 1rem;
  flex-grow: 1;
}

.project-features {
  margin-bottom: 1.5rem;
}

.project-features h4 {
  font-size: 0.9rem;
  font-weight: 600;
  margin-bottom: 0.75rem;
  color: var(--text-accent);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.features-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.5rem;
}

.feature-badge {
  background: var(--bg-accent);
  border: 1px solid var(--border-color);
  border-radius: 10px;
  padding: 0.5rem 1rem;
  font-size: 0.8rem;
  font-weight: 500;
  color: var(--text-secondary);
  text-align: center;
  transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  cursor: default;
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(5px);
}

.feature-badge::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(59, 130, 246, 0.1), transparent);
  transition: left 0.4s ease;
}

.feature-badge:hover::before {
  left: 100%;
}

.feature-badge:hover {
  background: var(--accent-color);
  color: white;
  border-color: var(--accent-color);
  transform: translateY(-2px) scale(1.05);
  box-shadow: 
    0 5px 15px rgba(59, 130, 246, 0.3),
    0 0 0 1px rgba(255, 255, 255, 0.2),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.project-tech {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
}

.tech-tag {
  background: var(--gradient-primary);
  color: white;
  padding: 0.5rem 1.25rem;
  border-radius: 25px;
  font-size: 0.85rem;
  font-weight: 600;
  transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  cursor: default;
  border: none;
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(10px);
}

.tech-tag::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: left 0.4s ease;
}

.tech-tag:hover::before {
  left: 100%;
}

.tech-tag:hover {
  transform: translateY(-3px) scale(1.05);
  box-shadow: 
    0 8px 25px rgba(0, 0, 0, 0.2),
    0 0 0 1px rgba(255, 255, 255, 0.2),
    inset 0 1px 0 rgba(255, 255, 255, 0.3);
}

.learn-more-btn {
  background: var(--gradient-primary);
  color: white;
  border: none;
  border-radius: 12px;
  padding: 1rem 1.75rem;
  font-weight: 600;
  font-size: 0.95rem;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  margin-top: auto;
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(10px);
  box-shadow: 
    0 4px 15px rgba(59, 130, 246, 0.2),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.learn-more-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: left 0.5s ease;
}

.learn-more-btn:hover::before {
  left: 100%;
}

.learn-more-btn:hover {
  transform: translateY(-3px) scale(1.05);
  box-shadow: 
    0 15px 35px rgba(59, 130, 246, 0.4),
    0 0 0 1px rgba(255, 255, 255, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.4);
}

/* Skills Section */
.skills {
  background: var(--bg-secondary);
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
}

.skill-category {
  background: var(--bg-primary);
  border-radius: 20px;
  padding: 2.5rem;
  text-align: center;
  border: 1px solid var(--border-color);
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(10px);
  transform: perspective(1000px) rotateX(0deg);
}

.skill-gradient-bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 6px;
  border-radius: 20px 20px 0 0;
  opacity: 0.8;
  transition: all 0.4s ease;
  z-index: 3;
}

.skill-category:hover .skill-gradient-bg {
  opacity: 1;
  height: 12px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
}

.skill-category::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
  transition: left 0.6s ease;
  z-index: 1;
}

.skill-category:hover::before {
  left: 100%;
}

.skill-category::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at center, var(--accent-color) 0%, transparent 70%);
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
  mix-blend-mode: soft-light;
}

.skill-category:hover::after {
  opacity: 0.05;
}

.skill-category:hover {
  transform: perspective(1000px) rotateX(-5deg) translateY(-10px) scale(1.03);
  box-shadow: 
    0 25px 50px rgba(0, 0, 0, 0.15),
    0 0 30px rgba(var(--accent-rgb), 0.2),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
  border-color: var(--accent-color);
  animation: skill-pulse 2.5s ease-in-out infinite alternate;
}

@keyframes skill-pulse {
  0% {
    box-shadow: 
      0 25px 50px rgba(0, 0, 0, 0.15),
      0 0 30px rgba(var(--accent-rgb), 0.2),
      inset 0 1px 0 rgba(255, 255, 255, 0.1);
  }
  100% {
    box-shadow: 
      0 30px 60px rgba(0, 0, 0, 0.2),
      0 0 50px rgba(var(--accent-rgb), 0.4),
      inset 0 1px 0 rgba(255, 255, 255, 0.15);
  }
}

.skill-icon {
  width: 70px;
  height: 70px;
  margin: 0 auto 1.5rem;
  background: var(--gradient-primary);
  border-radius: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 1.75rem;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  position: relative;
  overflow: hidden;
  box-shadow: 
    0 10px 25px rgba(0, 0, 0, 0.1),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.skill-icon::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.2) 0%, transparent 50%);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.skill-category:hover .skill-icon::before {
  opacity: 1;
}

.skill-category:hover .skill-icon {
  transform: translateY(-5px) scale(1.1) rotate(10deg);
  box-shadow: 
    0 15px 35px rgba(0, 0, 0, 0.2),
    0 0 0 1px rgba(255, 255, 255, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.4);
}

.skill-title {
  font-size: 1.25rem;
  font-weight: 700;
  margin-bottom: 1rem;
  color: var(--text-accent);
}

.skill-list {
  list-style: none;
  color: var(--text-secondary);
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  justify-content: center;
}

.skill-list li {
  margin-bottom: 0;
  padding: 0.5rem 1rem;
  background: var(--bg-accent);
  border-radius: 25px;
  transition: all 0.3s ease;
  font-size: 0.875rem;
  font-weight: 500;
  border: 1px solid var(--border-color);
}

.skill-list li:hover {
  background: var(--accent-color);
  color: white;
  transform: scale(1.05);
  border-color: var(--accent-color);
}

.project-card:nth-child(odd) {
  animation: float 6s ease-in-out infinite;
}

.project-card:nth-child(even) {
  animation: floatReverse 6s ease-in-out infinite;
}

.skill-category:nth-child(odd) {
  animation: float 4s ease-in-out infinite;
  animation-delay: 0.5s;
}

.skill-category:nth-child(even) {
  animation: floatReverse 4s ease-in-out infinite;
  animation-delay: 1s;
}

.profile-card {
  animation: pulse 8s ease-in-out infinite;
}

/* Contact Section */
.contact {
  background: var(--bg-primary);
}

.contact-content {
  max-width: 600px;
  margin: 0 auto;
}

.contact-form {
  background: var(--bg-secondary);
  border-radius: 25px;
  padding: 3rem;
  border: 1px solid var(--border-color);
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(10px);
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transform: perspective(1000px) rotateX(0deg);
}

.contact-form::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.05) 0%, transparent 50%);
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
}

.contact-form:hover::before {
  opacity: 1;
}

.contact-form:hover {
  transform: perspective(1000px) rotateX(-2deg) translateY(-5px);
  box-shadow: 
    0 30px 60px var(--shadow-medium),
    0 0 0 1px rgba(255, 255, 255, 0.1),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
  border-color: var(--accent-color);
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 600;
  align-items: center;
  display: flex;
  color: var(--text-accent);
}

.form-input,
.form-textarea {
  width: 100%;
  padding: 1.25rem;
  border: 2px solid var(--border-color);
  border-radius: 12px;
  background: var(--bg-primary);
  color: var(--text-primary);
  font-size: 1rem;
  transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  position: relative;
  backdrop-filter: blur(5px);
}

.form-input:focus,
.form-textarea:focus {
  outline: none;
  border-color: var(--accent-color);
  box-shadow: 
    0 0 0 4px rgba(59, 130, 246, 0.1),
    0 8px 25px rgba(0, 0, 0, 0.1),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
  transform: translateY(-2px) scale(1.02);
}

.form-input:hover,
.form-textarea:hover {
  border-color: var(--accent-color);
  transform: translateY(-1px);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.form-textarea {
  resize: vertical;
  min-height: 120px;
}

.submit-button {
  width: 100%;
  padding: 1.25rem;
  background: var(--gradient-primary);
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  position: relative;
  overflow: hidden;
  transform: perspective(1000px) rotateX(0deg);
}

.submit-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s ease;
}

.submit-button:hover::before {
  left: 100%;
}

.submit-button:hover {
  transform: perspective(1000px) rotateX(-5deg) translateY(-3px) scale(1.02);
  box-shadow: 
    0 15px 35px var(--shadow-medium),
    0 0 0 1px rgba(255, 255, 255, 0.2),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.submit-button:active {
  transform: perspective(1000px) rotateX(0deg) translateY(0px) scale(0.98);
}

.submit-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

/* Responsive Design */
/* Tablet View */
@media (max-width: 1024px) {
  .projects-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

@media (max-width: 768px) {
  .nav-menu {
    display: none;
  }
  
  .mobile-menu-toggle {
    display: block;
  }
  
  .hero-title {
    font-size: 2.5rem;
  }
  
  .hero-subtitle {
    font-size: 1.25rem;
  }
  
  .hero-cta {
    flex-direction: column;
    align-items: center;
  }
  
  .cta-button {
    width: 200px;
    text-align: center;
  }
  
  .section-title {
    font-size: 2rem;
  }
  
  .about-content {
    grid-template-columns: 1fr;
    gap: 3rem;
    text-align: center;
  }
  
  .about-intro h3 {
    font-size: 1.75rem;
  }
  
  .intro-highlight {
    font-size: 1.125rem;
  }
  
  .achievements-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .profile-card {
    padding: 2rem;
    transform: perspective(1000px) rotateY(0deg) rotateX(0deg);
  }
  
  .profile-avatar {
    width: 100px;
    height: 100px;
  }
  
  .avatar-text {
    font-size: 2rem;
  }
  
  .profile-name {
    font-size: 1.5rem;
  }
  
  .profile-role {
    font-size: 1rem;
  }
  
  .tech-badge {
    font-size: 0.8rem;
    padding: 0.2rem 0.6rem;
  }
  
  .projects-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
  }
  
  .project-card {
    min-height: auto;
  }
  
  .features-grid {
    grid-template-columns: 1fr;
  }
  
  .skills-grid {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }
  
  .container {
    padding: 0 1rem;
  }
  
  .section {
    padding: 3rem 0;
  }
}

@media (max-width: 480px) {
  .hero-title {
    font-size: 2rem;
  }
  
  .hero-subtitle {
    font-size: 1rem;
  }
  
  .hero-description {
    font-size: 1rem;
  }
  
  .section-title {
    font-size: 1.75rem;
  }
  
  .projects-grid {
    grid-template-columns: 1fr;
    gap: 1.25rem;
  }
  
  .project-content {
    padding: 1.5rem;
  }
  
  .project-header {
    flex-direction: column;
    gap: 1rem;
    align-items: flex-start;
  }
  
  .project-links {
    margin-left: 0;
    align-self: flex-end;
  }
  
  .project-card,
  .skill-category,
  .contact-form {
    padding: 1.5rem;
  }
  
  .about-content {
    gap: 2rem;
  }
  
  .about-intro {
    padding: 1.5rem;
  }
  
  .about-intro h3 {
    font-size: 1.5rem;
  }
  
  .intro-highlight {
    font-size: 1rem;
  }
  
  .about-description p {
    font-size: 1rem;
  }
  
  .achievement-card {
    padding: 1rem;
    flex-direction: column;
    text-align: center;
    gap: 0.75rem;
  }
  
  .achievement-icon {
    width: 40px;
    height: 40px;
  }
  
  .profile-card {
    padding: 1.5rem;
  }
  
  .profile-avatar {
    width: 80px;
    height: 80px;
  }
  
  .avatar-text {
    font-size: 1.5rem;
  }
  
  .profile-name {
    font-size: 1.25rem;
  }
  
  .stat-number {
