/* Animation utilities */
.fade-in {
  opacity: 0;
  transform: translateY(30px);
  transition: all 0.6s ease;
}

.fade-in.visible {
  opacity: 1;
  transform: translateY(0);
}

/* Smooth scrolling for anchor links */
html {
  scroll-behavior: smooth;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: var(--bg-secondary);
}

::-webkit-scrollbar-thumb {
  background: var(--accent-color);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--accent-hover);
}

/* Go to Top FAB Button */
.go-to-top {
  position: fixed;
  bottom: 30px;
  right: 30px;
  width: 56px;
  height: 56px;
  background: linear-gradient(135deg, var(--accent-color), rgba(139, 92, 246, 0.9));
  border: none;
  border-radius: 50%;
  color: white;
  font-size: 1.5rem;
  cursor: pointer;
  box-shadow: 
    0 8px 25px rgba(139, 92, 246, 0.3),
    0 4px 15px rgba(0, 0, 0, 0.2);
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  transform: translateY(20px) scale(0.8);
  transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(196, 181, 253, 0.2);
}

.go-to-top.visible {
  opacity: 1;
  visibility: visible;
  transform: translateY(0) scale(1);
}

.go-to-top:hover {
  transform: translateY(-5px) scale(1.1);
  box-shadow: 
    0 12px 35px rgba(139, 92, 246, 0.4),
    0 6px 20px rgba(0, 0, 0, 0.3);
  background: linear-gradient(135deg, rgba(139, 92, 246, 1), rgba(168, 85, 247, 0.95));
}

.go-to-top:active {
  transform: translateY(-2px) scale(1.05);
}

.go-to-top svg {
  animation: bounce 2s ease-in-out infinite;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-3px);
  }
  60% {
    transform: translateY(-2px);
  }
}

/* Professional Custom Cursor Styles */
* {
  cursor: default;
}

body {
  cursor: default;
}

a, button, .clickable, .go-to-top {
  cursor: pointer;
  transition: all 0.2s ease;
}

a:hover, button:hover, .clickable:hover {
  cursor: pointer;
}

input, textarea {
  cursor: text;
}

input:focus, textarea:focus {
  cursor: text;
}

/* Professional hover effects */
.project-card, .skill-card, .achievement-card {
  cursor: pointer;
  transition: all 0.3s ease;
}

.project-card:hover, .skill-card:hover, .achievement-card:hover {
  cursor: pointer;
  transform: translateY(-2px);
}

/* Navigation and interactive elements */
.nav-link, .social-link, .cta-button {
  cursor: pointer;
  position: relative;
  transition: all 0.2s ease;
}

.nav-link:hover, .social-link:hover, .cta-button:hover {
  cursor: pointer;
}

/* Form elements professional styling */
.contact-form input, .contact-form textarea {
  cursor: text;
  transition: border-color 0.2s ease;
}

.contact-form input:focus, .contact-form textarea:focus {
  cursor: text;
  outline: none;
  border-color: var(--accent-color);
}

/* Disabled state */
button:disabled, input:disabled, textarea:disabled {
  cursor: not-allowed;
  opacity: 0.6;
}

/* Loading state */
.loading, .processing {
  cursor: wait;
}

/* Drag and drop areas */
.drag-area {
  cursor: grab;
}

.drag-area:active {
  cursor: grabbing;
}

/* Text selection areas */
.selectable-text {
  cursor: text;
  user-select: text;
}

/* Professional resize cursors for responsive elements */
.resizable-horizontal {
  cursor: ew-resize;
}

.resizable-vertical {
  cursor: ns-resize;
}

.resizable-diagonal {
  cursor: nw-resize;
}

/* Additional cosmic background effects */
.hero-background::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 30%, rgba(196, 181, 253, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 80% 70%, rgba(139, 92, 246, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 40% 80%, rgba(168, 85, 247, 0.08) 0%, transparent 50%);
  animation: cosmicDrift 25s ease-in-out infinite;
  z-index: 1;
}

.hero-background .cosmic-stars {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: 
    radial-gradient(2px 2px at 20px 30px, rgba(255, 255, 255, 0.3), transparent),
    radial-gradient(2px 2px at 40px 70px, rgba(196, 181, 253, 0.4), transparent),
    radial-gradient(1px 1px at 90px 40px, rgba(255, 255, 255, 0.2), transparent),
    radial-gradient(1px 1px at 130px 80px, rgba(139, 92, 246, 0.3), transparent),
    radial-gradient(2px 2px at 160px 30px, rgba(255, 255, 255, 0.25), transparent);
  background-repeat: repeat;
  background-size: 200px 100px;
  animation: twinkle 4s ease-in-out infinite alternate;
  z-index: 2;
}

@keyframes cosmicDrift {
  0% { transform: translateX(-30px) translateY(-30px) rotate(0deg); }
  25% { transform: translateX(20px) translateY(-50px) rotate(90deg); }
  50% { transform: translateX(-10px) translateY(-20px) rotate(180deg); }
  75% { transform: translateX(30px) translateY(-40px) rotate(270deg); }
  100% { transform: translateX(-30px) translateY(-30px) rotate(360deg); }
}

@keyframes twinkle {
  0%, 100% { opacity: 0.3; transform: scale(0.9); }
  50% { opacity: 0.8; transform: scale(1.1); }
}

@keyframes nebula {
  0% { transform: rotate(0deg) scale(1); opacity: 0.1; }
  50% { transform: rotate(180deg) scale(1.3); opacity: 0.2; }
  100% { transform: rotate(360deg) scale(1); opacity: 0.1; }
}

/* Floating geometric shapes */
.hero-geometry {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
  z-index: 3;
}

.floating-shape {
  position: absolute;
  border-radius: 50%;
  background: linear-gradient(45deg, rgba(196, 181, 253, 0.1), rgba(139, 92, 246, 0.1));
  animation: floatAndRotate 20s ease-in-out infinite;
}

.floating-shape:nth-child(1) {
  width: 60px;
  height: 60px;
  top: 20%;
  left: 10%;
  animation-delay: 0s;
  animation-duration: 18s;
}

.floating-shape:nth-child(2) {
  width: 40px;
  height: 40px;
  top: 60%;
  right: 15%;
  animation-delay: -5s;
  animation-duration: 22s;
}

.floating-shape:nth-child(3) {
  width: 80px;
  height: 80px;
  bottom: 25%;
  left: 20%;
  animation-delay: -10s;
  animation-duration: 25s;
}

.floating-shape:nth-child(4) {
  width: 50px;
  height: 50px;
  top: 30%;
  right: 30%;
  animation-delay: -15s;
  animation-duration: 20s;
}

@keyframes floatAndRotate {
  0% { 
    transform: translateY(0px) translateX(0px) rotate(0deg) scale(0.8);
    opacity: 0.3;
  }
  25% { 
    transform: translateY(-40px) translateX(20px) rotate(90deg) scale(1.2);
    opacity: 0.6;
  }
  50% { 
    transform: translateY(-20px) translateX(-15px) rotate(180deg) scale(1);
    opacity: 0.4;
  }
  75% { 
    transform: translateY(-60px) translateX(30px) rotate(270deg) scale(1.1);
    opacity: 0.7;
  }
  100% { 
    transform: translateY(0px) translateX(0px) rotate(360deg) scale(0.8);
    opacity: 0.3;
  }
}
